.b-light-gallery {
  margin: 30px 0; }
  .b-light-gallery__items {
    display: flex;
    flex-wrap: wrap; }
  .b-light-gallery__item {
    width: 100%;
    position: relative;
    max-height: 250px;
    overflow: hidden; }
  .b-light-gallery__item-title {
    position: absolute;
    left: 0;
    bottom: 15px;
    background: #555555;
    width: 160px;
    min-height: 50px;
    color: #fff;
    display: inline-flex;
    padding: 0 15px;
    align-items: center;
    transition: background .3s;
    backface-visibility: hidden;
    word-break: break-word; }
  .b-light-gallery__item-img {
    max-width: 100%;
    width: 100%;
    transition: transform .3s;
    backface-visibility: hidden; }
  .b-light-gallery__item-link:hover .b-light-gallery__item-title {
    background: #333333; }
  .b-light-gallery__item-link:hover .b-light-gallery__item-img {
    transform: scale(1.15); }
  @media (min-width: 480px) {
    .b-light-gallery__items.is-grid .b-light-gallery__item {
      width: 50%; } }
  @media (min-width: 992px) {
    .b-light-gallery__items.is-grid .b-light-gallery__item {
      width: 33.33333%; }
    .b-light-gallery__item-title {
      width: 65%; } }
